import React, { useState } from "react";
import PropTypes from "prop-types";

import BasicFileInput from 'common/BasicFileInput'
import BasicInput from 'common/BasicInput'
import { makeStylesWithTheme } from 'common/EditablesContext'

const DEFAULT_BACKGROUND_IMAGE = "data:image/jpeg;base64,"

const useStyles = makeStylesWithTheme(theme => ({
  fileUploadForm: {
    margin: theme.spacing(3)
  },
  formField: {
    marginBottom: theme.spacing(3)
  }
}))

const ImageUploadEditor = ({
  content={},
  onContentChange,
  maxSize,
  uploadFile,
  mimetypes='image/*'
}) => {
  const classes = useStyles()

  const handleCaptionChange = event => {
    const caption = event.currentTarget.value;

    onContentChange({
      ...content,
      caption: caption
    })
  }

  const handleTitleChange = event => {
    const title = event.currentTarget.value;

    onContentChange({
      ...content,
      title: title
    })
  }

  const handleImageChange = ({ filename, filepath }) => {
    onContentChange({
      ...content,
      filepath,
      filename
    })
  }

  const { filepath, filename, title='', caption='' } = content;

  return (
    <div className={classes.fileUploadForm}>
      <div className={classes.formField}>
        <BasicFileInput
          label="Select image"
          mimetypes={mimetypes}
          onChange={handleImageChange}
          autoFocus={true}
          name="file-upload"
          maxSize={maxSize}
          uploadFile={uploadFile}
        />
      </div>
      <div className={classes.formField}>
        <BasicInput
          name="file-title"
          label="Alt text"
          value={title}
          onChange={handleTitleChange}
          autoFocus={false}
          helpText="Write a description for the image. This should only be left empty if the image is purely decorative."
        />
      </div>
      <div className={classes.formField}>
        <BasicInput
          name="file-caption"
          label="Caption (optional)"
          value={caption}
          onChange={handleCaptionChange}
          autoFocus={false}
        />
      </div>
    </div>
  );
}

ImageUploadEditor.propTypes = {
  content: PropTypes.shape({
    filepath: PropTypes.string,
    filename: PropTypes.string,
    caption: PropTypes.string,
    title: PropTypes.string
  }),
  classes: PropTypes.string,
  uploadFile: PropTypes.func.isRequired,
  onContentChange: PropTypes.func.isRequired
}


export default ImageUploadEditor;
