import React from "react";
import PropTypes from "prop-types";
import Editable from "common/Editable";
import { makeStylesWithTheme } from 'common/EditablesContext'

const useStyles = makeStylesWithTheme(theme => ({
  bgImg: {
    backgroundImage: props => `url('${props.filepath}')`,
    height: 'inherit',
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover"
  }
}))

const EditableBackgroundImage = ({ content, onSave, children, ...rest }) => {
  const { filepath, filename, title, caption } = content;
  const classes = useStyles({ filepath })

  return (
    <Editable
      loader={() => import("editingTools/ImageUploadEditor")}
      onSave={onSave}
      content={content}
      showChildren
      fullWidth
      { ...rest }
    >
      <div className={classes.bgImg}>
        {children}
      </div>
    </Editable>
  );
};

EditableBackgroundImage.propTypes = {
  content: PropTypes.shape({ filepath: PropTypes.string }).isRequired,
  onSave: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  maxSize: PropTypes.number,
  styles: PropTypes.object,
  classes: PropTypes.string,
  EditorProps: PropTypes.shape({ image: PropTypes.object }),
}

EditableBackgroundImage.defaultProps = {
  content: { filepath: `data:image/jpeg;base64,` },
  onSave: content => console.log('Implement a function to save changes!', content),
  maxSize: 1024 * 1024 * 2, // 2MB
  styles: {},
  EditorProps: { image: {} },
}

export default EditableBackgroundImage;